<template>
    <div page-index>
        <p v-shadow>姓名: {{ name }}</p>
        <p v-shadow>性别: {{ sex }}</p>
        <p v-shadow>邮箱: {{ email }}</p>
        <p>图片的根地址: {{ baseimg }}</p>
        <p>
            <img
                :src="`${baseimg}/nv.jpeg`"
                v-head-img />
        </p>
        <p>
            <img
                :src="`${baseimg}/wall02.jpg`"
                v-img />
        </p>
        <!-- 过渡动画 -->
        <transition
            appear
            enter-active-class="animate__animated animate__bounce">
            <div v-card="450">
                <p>关于main.js中rander函数的说明</p>
                <div>
                    <pre>
render函数是为了解决引入非完整版vue.js无法编译template的问题
Vue模块中引入的是vue.runtime.esm.js而非vue.js
不支持template: '&lt;div&gt;&lt;/div&gt;'写法
需要借助render函数渲染模板
render函数的原始写法:
render: function(createElement) {
    return createElement();
}
=>
render: h => h();
                </pre
                    >
                </div>
            </div>
        </transition>
        <div v-card="450">
            <p>Vue为何不引入完整版vue.js而是精简版vue.runtime.esm.js</p>
            <div>
                <pre>
首先vue.js包含核心功能和模板解析器两个部分, vue.runtime.esm.js只包含核心部分, 当进行打包发布时, 使用vue.js编译后的文件会比较大
其次, 模板解析器在生产环境下是不需要的, 所以Vue提供了一个精简版的js以便在打包时可以节省部分空间
而组件中的&lt;template&gt;标签由vue-template-compiler库解析
                </pre>
            </div>
        </div>
        <div
            v-card
            data-has-a>
            <p>入口文件是否可以不使用main.js命名?</p>
            <div>
                <pre>
of course
在vue.config.js中配置如下内容:
pages: {
    index: {
        entry: 'src/app.js',
    },
},
entry: 即入口文件信息
官网配置文档: [<a href="https://cli.vuejs.org/zh/config" target="_blank">https://cli.vuejs.org/zh/config</a>]
                </pre>
            </div>
        </div>
        <div v-card>
            <p>ref属性</p>
            <div>
                <pre>
ref是由Vue提供的一个组件标识属性, 可以理解为id属性的替代者
可以使用this.$refs获取标识的元素
与id属性不同的是: 当在组件上使用ref属性时, this.$refs获取的是组件的实例VueComponent而非元素本身
                </pre>
            </div>
        </div>
        <div v-card>
            <p>Vue过渡动画</p>
            <div>
                <pre>
在需要过度的元素外使用transition标签包裹
&lt;transition appear&gt;
...
&lt;transition/&gt;
集成第三方库
npm i animate.css
import 'animate.css'
&lt;transition
    appear
    enter-active-class="animate__animated animate__bounce"&gt;
...
&lt;transition/&gt;
                </pre>
            </div>
        </div>
        <div v-card>
            <p>一个好看的背景</p>
            <div>
                <pre>
background: linear-gradient(
                90deg,
                rgba(26, 26, 26, 9) 0,
                rgba(26, 26, 26, 9) 98%,
                hsla(0, 0%, 100%, 0) 98%,
                hsla(0, 0%, 100%, 0) 100%
            )
            50% 0,
        linear-gradient(
                0deg,
                rgba(26, 26, 26, 9) 0,
                rgba(26, 26, 26, 9) 98%,
                hsla(0, 0%, 100%, 0) 98%,
                hsla(0, 0%, 100%, 0) 100%
            )
            0 50%,
        #363663;
background-size: 80px 80px;
                </pre>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Index',
    data() {
        return {
            name: 'naihe',
            sex: '一个帅哥',
            email: 'naiheemail@yeah.net',
        };
    },
};
</script>

<style scoped>
/* div {
    max-width: 100%;
    min-height: 100%;
    border-radius: 8px;
    background-color: #b1d5c8;
    padding: 18px;
} */

.spin {
    animation: xuanzhuan 5s linear;
}

@keyframes xuanzhuan {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(359deg);
    }
}

[animate-spin]:hover {
    animation: xuanzhuan 1s linear;
}

.v-enter-active {
    animation: init 0.5s;
}

.v-leave-active {
    animation: init 0.5s reverse;
}

@keyframes init {
    0% {
        transform: scale(0);
    }
    50% {
        transform: scale(1.5);
    }
    100% {
        transform: scale(1);
    }
}
</style>
